    position: absolute;
    left: 25%;
    right: 25%;
    top: 10%;
    max-height: 80%;
    overflow: auto;
    margin: auto;
    background-color: var(--primary-color);
    color: var(--secondary-color);
    border: 1px solid var(--secondary-color);
    border-radius: 0.4rem;
    scrollbar-color: var(--secondary-color) var(--primary-color);
    text-align: center;
}

/* styling for popup scrollbar (only available in chrome) */
.popup-content::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    background-color: var(--primary-color);
    /* background-color: red; */
    border-top-right-radius: 0.4rem;
    border-bottom-right-radius: 0.4rem;
}

.popup-content::-webkit-scrollbar {
    width: 15px;
    background-color: var(--primary-color);
    border-top-right-radius: 0.4rem;
    border-bottom-right-radius: 0.4rem;
}

.popup-content::-webkit-scrollbar-thumb {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    background-color: var(--secondary-color);
    /* border: 1px solid var(--secondary-color); */
    /* border-left: 1px solid var(--secondary-color); */
    border-left: 1px solid var(--primary-color);
    border-top-right-radius: 0.3rem;
    border-bottom-right-radius: 0.3rem;
}

.popup-delay {
    --animation-delay: 1.2s;
    opacity: 0;
    /* visibility: hidden; */
    animation: 1s linear var(--animation-delay) forwards delayedOpacity;
}

@keyframes delayedOpacity {
    from {
        opacity: 0;
        /* visibility: hidden; */
    }

    to {
        opacity: 1;
        /* visibility: visible;  */
    }
}


/* Categories Mode & Rapid Mode Styling*/
.item-list {
    display: flex;
    justify-content: space-evenly;
    align-content: center;
    flex-wrap: wrap;
    padding: 10px 0;
    width: 80%;
    margin: auto;
    margin-bottom: 20px;
}

.item-list button {
    font-size: 1.3rem;
    line-height: 2rem;
    background-color: var(--primary-color);
    color: var(--secondary-color);
    border: 1px solid var(--secondary-color);
    border-radius: 0.2rem;
    cursor: pointer;
    padding: 10px;
    margin: 10px;
    transition: 0.2s ease-out;
}

.item-list button img {
    width: 35px;
    vertical-align: middle;
    margin-right: 5px;
}

.item-list button:hover {
    transform: scale(1.025);
    transition: 0.2s ease-out;
    box-shadow: 0px 0px 10px 1px var(--secondary-color);
}

.item-list button:active {
    transform: scale(1.01);
    transition: 0.2s ease-out;
}