:root {
  --light: rgb(230, 230, 230);
  --dark: rgb(45, 45, 45);

  --light-detail: rgb(250, 250, 250);
  --dark-detail: rgb(20, 20, 20);

  --high-contrast-correct: rgb(245, 121, 58);
  --high-contrast-semi: rgb(133 192 249);
}

* {
  font-family: 'Nunito', sans-serif;
}

html,
body {
  min-height: 100%;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  /* margin: 0; */
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

