/* This is for Info & Stats popup */
.main {
    margin: auto;
    font-size: 1.1rem;
    font-weight: 600;
}

.popup-content h1 {
    margin-block-start: 0.6em;
    margin-block-end: 0.2em;
    display: inline-block;
    padding: 0 10px;
    border-bottom: 3px solid black;
    border-color: var(--secondary-color);
    font-size: 2rem;
}

/* Info */
.info .main {
    max-width: 80ch;
    padding: 0 10px;
}

.info .main h3 {
    margin-block-start: 1.2em;
    margin-block-end: 1em;
    text-decoration: underline;
}

.info .main ul, .text {
    padding-inline-start: 40px;
}

.text,
.input-list,
.rules-list,
.modes-list,
.letter-info-list {
    text-align: left;
    margin-block-start: 1em;
    margin-block-end: 1em;
}

.modes-list>li {
    text-decoration: underline;
}

.modes-list>div {
    margin-left: 15px;
    margin-bottom: 10px;
}

.letter-info-list>li {
    list-style: none;
    margin: 5px 0;
}

.letter-info {
    display: inline-block;
    width: 1.6rem;
    height: 1.6rem;
    margin-right: 10px;
    vertical-align: middle;
    border: 1px solid var(--secondary-color);
    border-radius: 0.2rem;
    text-align: center;
    /* color: var(--primary-color); */
    color: var(--secondary-color);
    font-size: 1.25rem;
    line-height: 1.7rem;
}