/* Stats */
.stats .main {
    display: flex;
    align-items: stretch;
    margin: 20px auto;
}

.stats .main .game-stats {
    flex: 1;
    margin: auto;
}


/* game-stats */
.wins-and-losses {
    display: inline-block;
    margin-left: 5px;
}

.wins-and-losses .wins {
    color: green;
    text-shadow: 0 0 1px green;
}

.wins-and-losses .losses {
    color: red;
    text-shadow: 0 0 1px red;
}

.streakNumber {
    --blue: rgb(0, 146, 214);
    color: var(--blue);
    text-shadow: 0 0 1px var(--blue);
}

.rapidStreak {
    margin-top: 20px;
}

.rapidStreakList {
    display: flex;
    max-width: 80%;
    margin: auto;
}

.rapidStreakList>* {
    flex: 1;
    /* border: 2px solid var(--secondary-color); */
    /* box-shadow: 0 0 5px var(--secondary-color); */
    border-radius: 0.2rem;
    margin: 0 15px;
}



/* divider */
.stats .main .divider {
    width: 2px;
    background: var(--secondary-color);
}


/* Histogram */
.stats .main .histogram {
    flex: 1;
    margin: auto;
}

.histogram-bar-item {
    margin: 5px 0;
}

.histogram-bar-item>* {
    display: inline-block;
    margin: 0 5px;
}

.histogram-bar-wrapper {
    width: 70%;
}

.histogram-bar {
    background-color: var(--secondary-color);
    color: var(--primary-color);
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}


/* Timer */
.next-word-timer {
    margin-bottom: 20px;
    font-size: 1.1rem;
    font-weight: 600;
}

.next-word-timer h3 {
    text-decoration: underline;
}